@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
:root {
	--color-primary-shade: #1F2226;
	--color-primary-shade-light: #626C7C;
  }

* {
	box-sizing: border-box;
	outline: none
}

/* Fonts */

html, body, #root {
	padding: 0;
	margin: 0;
	color: #1F2226;
	font-family: 'Roboto', sans-serif;
	width: 100vw;
	height: 100vh;
	background: #f2f2f2;
}

/* Theme colors*/

/* Blue shades */

.color-primary { 
	background: #2E6AD1;
	color: white
}
.color-primary-light { 
	background: #75A2ED;
	color: white
}
.color-primary-dark { 
	background: #0C3882;
	color: white
}

/* Green shades */

.color-secondary{ 
	background: #1FDE49;
	color: white
}
.color-secondary-light { 
	background: #6BF289;
	color: white
}
.color-secondary-dark { 
	background: #009421;
	color: white
}

/* Purple shades */

.color-terciary { 
	background: #7A2CD3;
	color: white
}
.color-terciary-light { 
	background: #AD74EE;
	color: white
}
.color-terciary-dark { 
	background: #440A85;
	color: white
}

.color-complement { 
	background: #FFB124;
	color: white
}	/* Orange shades */
.color-complement-light { 
	background: #FFCC71;
	color: white
}
.color-complement-dark { 
	background: #C47E00;
	color: white
}

/* Grey shades */

.color-primary-shade { 
	background: #1F2226;
	color: white
}
.color-primary-shade-light { 
	background: #626C7C;
	color: white
}
.color-primary-shade-dark { 
	background: #090D14;
	color: white
}

input {
	padding: 10px;
	border: solid 1px #ccc;
	border-radius: 5px;
}

button, .button {
	border: none;
	padding: 10px;
	cursor: pointer;
	border-radius: 5px;
}

.modalBlackWindow {
	display: none;
	position: absolute;
	top: 0;
	width: 100vw;
	height: 100vh;
	justify-content: center;
	align-items: center;
	background-color: #08080856;
}

.modalBlackWindow.active {
	display: flex;
}

.modalBlackWindow .modal {
	position: relative;
	min-width: 300px;
	padding: 10px;
	background: white;
	border-radius: 5px;
	padding-top: 15px;
	padding-right: 20px;
}

.modalBlackWindow .modal .closeBtn {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 0;
	right: 0;
	padding: 5px;
	width: 25px;
	height: 25px;
	background: transparent;
}

.App {
	text-align: center;
}

.input-group {
	display: flex;
	flex-direction: column;
	margin-top: 15px;
}

.loginContainer {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}

.loginForm {
	padding: 10px;
	border: solid 1px #ccc;
	border-radius: 5px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
	min-width: 400px;
}

.loginForm header {
	display: flex;
	justify-content: center;
}

.loginForm header .logoContainer {
	text-decoration: none;
	color: unset;
	display: grid;
	grid-template-columns: 80px auto;
	grid-gap: 15px;
}

.loginForm header img {
	width: 80px;
	height: 80px;
	object-fit: cover;
	border-radius: 5px;
}

.loginButton {
	width: 100%;
	margin-top: 15px;
}
.dashboard {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 10px;
    padding-top: 55px;
    grid-gap: 10px;
}


.dashboard div {
    min-height: 240px;
    padding: 10px;
    background: white;
    border-radius: 5px;
    border: solid 1px #ccc;
    color: #888;
}

.dashboard .graphDash {
    grid-column-start: 2;
    grid-column-end: 4;
}

.dashboard div h1 {
    font-size: 4em;
    margin: 0;
    padding: 0;
    text-align: center;
}

.dashboard div h2 {
    font-size: 2em;
    font-weight: 600;
    text-align: center;
}
div.supMenu {
	cursor: default;
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	font-size: 10pt;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	overflow: visible;
	color: white;
	background-color: var(--color-primary-shade);
}

div.supMenu ul {
	display: flex;
	flex-direction: row;
	position: relative;
	padding: 0;
	margin: 0;
}

div.supMenu li {
	position: relative;
	display: flex;
	flex-direction: column;
	overflow: visible;
	background: var(--color-primary-shade);
}

div.supMenu li a {
	color: white;
	cursor: default;
	padding: 5px;
	text-decoration: none;
}

div.supMenu li a:hover {
	background: var(--color-primary-shade-light);
}

div.supMenu li ul.internalDropDown {
	position: absolute;
	top:27px;
	display: none;
	flex-direction: column;
	height: 0;
	overflow: visible;
}

div.supMenu li ul.internalDropDown li {
	width: 100%;
}

div.supMenu li:hover ul.internalDropDown {
	display: flex;
}
